import {reactive,toRefs,defineComponent} from 'vue';
import {useRouter} from 'vue-router';
import {postRequest} from '../../request/api';
import url from '../../request/url';
import validate from '../../libs/validate';
import {Toast,Dialog} from 'vant';
import BgBox from '../../components/BgBox';

export default defineComponent({
    name:'secure',
    components:{
        BgBox
    },
    setup () {
        let state = reactive({
            isOpen: false,
            isPassword: true,
            password: {
                oldPassword: "",
                newPassword: ""
            }
        })

        let {back,openPasswordBox,destroy,logout,toggleType,commit} = methods(state);

        return {
            ...toRefs(state),
            back,
            openPasswordBox,
            destroy,
            logout,
            toggleType,
            commit
        }
    }
})

function methods(state) {
    let router = useRouter();

    let back = () => {
        router.back();
    }

    let openPasswordBox = () => {
        Reflect.set(state,'isOpen',true);
    }

    let destroyAccount = () => {
        postRequest(url.destroyAccount).then(({code,msg}) => {
            if(code == 'G001') router.push({ name: "login" });
            sessionStorage.removeItem('token');
            Toast(msg);
        }).catch((err) => {
            console.log("err==>",err);
        });
    }

    //注销账号
    let destroy = () => {
        Dialog.confirm({
            title: '注销账号',
            message:'是否确定注销账号，一旦注销无法恢复！'
        }).then(() => {
            destroyAccount();
        }).catch(() => {
            Toast('取消注销账号');
        });
    }
    //登出
    let logout = () => {
        Dialog.confirm({
            title: '退出登录',
            message:'是否确定退出登录！'
        }).then(() => {
            sessionStorage.removeItem('token');
            router.push({ name: "login" });
        }).catch(() => {
            Toast('取消退出登录');
        });
    }

    let toggleType = () => {
        Reflect.set(state,'isPassword',!state.isPassword);
    }

    let commit = () => {
        let o = {
            oldPassword: {
                value: state.password.oldPassword,
                errorMsg: "旧密码由数字字母下划线组合(6-16字符)",
                reg: /^[A-Za-z]\w{5,15}$/
            },
            newPassword: {
                value: state.password.newPassword,
                errorMsg: "密码由数字字母下划线组合(6-16字符)",
                reg: /^[A-Za-z]\w{5,15}$/
            },
        };

        let isPass = validate.valid(o);
        if(isPass){
            if(state.password.newPassword == state.password.oldPassword) return Toast('新密码和旧密码不能相同');
            let data = Object.create(null);
            Reflect.set(data,'password',state.password.newPassword);
            Reflect.set(data,'oldPassword',state.password.oldPassword);
            postRequest(url.updatePassword,data).then(({code,msg}) => {
                if(code == 'E001') {
                    sessionStorage.removeItem('token');
                    router.push({ name: "login" })
                };
                Toast(msg);
            }).catch((err) => {
                console.log("err==>",err);
            });
        }


    }

    return{
        back,
        openPasswordBox,
        destroy,
        logout,
        toggleType,
        commit
    }
}