<template>
    <div class="secure">
        <van-nav-bar title="安全中心" left-text="返回" left-arrow fixed @click-left="back" />
        <bg-box>
            <van-cell title="修改密码" center is-link @click="openPasswordBox" />
            <van-cell title="注销账号" center is-link @click="destroy" />
        </bg-box>
        <div class="logout-box">
            <van-button color="#0C34BA" block round @click="logout">退出登录</van-button>
        </div>

        <van-popup v-model:show="isOpen" closeable position="bottom">
            <div class="form-box">
                <div class="form-title">修改密码</div>
                <van-form>
                    <van-cell-group inset>
                        <van-field
                            v-model="password.oldPassword"
                            :type="isPassword ? 'password' : 'text'"
                            name="旧密码"
                            label="旧密码"
                            placeholder="旧密码(首字符必须为字母)"
                            :right-icon="isPassword ? 'closed-eye' : 'eye-o'"
                            autocomplete="off"
                            @click-right-icon="toggleType"
                        />
                        <van-field
                            v-model="password.newPassword"
                            :type="isPassword ? 'password' : 'text'"
                            name="新密码"
                            label="新密码"
                            placeholder="新密码(首字符必须为字母)"
                            :right-icon="isPassword ? 'closed-eye' : 'eye-o'"
                            autocomplete="off"
                            @click-right-icon="toggleType"
                        />
                    </van-cell-group>
                    <div class="commit-btn">
                        <van-button round block color="#0C34BA" @click="commit">提交</van-button>
                    </div>
                </van-form>
            </div>
        </van-popup>
    </div>
</template>

<script>
    export { default } from './secure';
</script>

<style lang="less" scoped>
    @import './secure.less';
</style>